$basic: #1E1E1E;
$orange: #FFB800;
$grey: #5B7183;
$blue: #2F80ED;

body {
  font-family: 'DIN Pro', sans-serif;
  font-size: 18px;
  line-height: calc(25 / 18);
  color: $basic; }

.container {
  margin: 0 auto;
  padding: 0 20px;
  position: relative; }

a {
  text-decoration: none;
  color: $blue; }

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 150px;
  height: 40px;
  position: relative;
  font-weight: 500;
  line-height: 21px;
  text-align: center;
  color: $basic;
  &_yellow {
    &::before, &::after {
      @include p;
      width: 100%;
      height: 100%;
      transition: .25s linear; }
    &::before {
      background: $orange;
      z-index: 10;
      top: 0;
      left: 0; }
    &::after {
      border: 2px solid #A46200;
      top: 8px;
      left: 8px;
      z-index: 0; }
    &:hover {
      background: #A46200;
      color: #fff;
      &::before {
        opacity: 0; }
      &::after {
        top: 0;
        left: 0; } } }
  span {
    position: relative;
    z-index: 20;
    margin-top: -2px; }
  &_white {
    height: 40px;
    width: 260px;
    border: 2px solid rgba(#fff, 0.5);
    font-size: 24px;
    line-height: 28px;
    color: #fff;
    &:hover {
      background: #fff;
      color: $basic; } }
  &_blue {
    border: 2px solid rgba($blue, .5);
    font-size: 24px;
    line-height: 25px;
    color: $blue;
    &:hover {
      background: $blue;
      color: #fff; } }
  &_grey {
    border: 2px solid rgba(91, 113, 131, 0.5);
    color: $grey;
    span {
      margin-top: -4px; }
    &:hover {
      background: rgb(91, 113, 131);
      color: #fff; } } }

.subheader {
  font-size: 36px;
  font-weight: 700;
  line-height: 40px; }

#top {
  width: 50px;
  height: 50px;
  background: url('../img/about-item-bg.svg') no-repeat;
  background-size: contain; }

.header {
  background: #000 url('../img/header-bg.jpg') no-repeat top center;
  background-size: cover;
  color: #fff;
  padding: 30px 0 0;
  .container {
    max-width: 1438px; } }

.logo {
  margin-right: 111px;
  a {
    display: block;
    line-height: 0;
    margin-bottom: 6px; }
  img {
    width: 240px; } }

.tagline {
  font-weight: 500;
  line-height: 21px;
  color: rgba(#fff, .5);
  text-align: right; }

.menu {
  margin: 50px 85px 0 0;
  & > ul {
    & > li {
      margin: 0 20px;
      &.current-menu-item {
        & > a {
          &::after {
            opacity: 1;
            top: calc(100% + 4px); } } }
      & > a {
        font-size: 24px;
        font-weight: 500;
        line-height: 28px;
        color: #fff;
        position: relative;
        &::after {
          @include p;
          height: 3px;
          width: 100%;
          left: 0;
          background: $orange;
          opacity: 0;
          top: 50%;
          @include tr; }
        &:hover {
          &::after {
            opacity: 1;
            top: calc(100% + 4px); } } } } } }

.contacts {
  margin: 50px auto 0 0;
  &__phone {
    font-size: 24px;
    font-weight: 500;
    line-height: 28px;
    color: #fff;
    &:hover {
      color: $orange; } }
  &__whours {
    font-weight: 500;
    line-height: 21px;
    color: rgba(#fff, .5);
    margin-top: 6px; } }

.callback {
  margin-top: 45px; }

.intro {
  padding: 85px 0 100px;
  &__header {
    font-size: 96px;
    font-weight: 700;
    line-height: 100px;
    text-transform: uppercase;
    margin-bottom: 39px;
    position: relative;
    z-index: 20;
    max-width: 850px; }
  ul {
    margin-bottom: 40px;
    max-width: 500px;
    & > li {
      font-size: 36px;
      font-weight: 500;
      line-height: 40px;
      padding-left: 30px;
      position: relative;
      &::before {
        @include p;
        width: 23px;
        height: 23px;
        background: url('../img/sprite.svg#check') no-repeat;
        background-size: 23px;
        top: 9px;
        left: -3px; } } }
  &__button {
    width: 300px;
    height: 60px;
    font-size: 36px;
    line-height: 42px; }
  .container {
    position: relative; }
  &__video {
    position: absolute;
    // width: 890px
    width: 900px;
    // height: 660px
    height: 500px;
    top: -21px;
    right: -21px;
    z-index: 10;
    cursor: pointer;
    &-inner {
      position: relative;
      width: 100%;
      height: 100%;
      background: #000;
      &.paused::after {
        @include p;
        width: 100px;
        height: 100px;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        border-radius: 50%;
        background: url('../img/sprite.svg#play') no-repeat center;
        background-size: 100px;
        @include tr; }
      &:hover {
        &::after {
          opacity: .75; } } }
    video {
      width: 100%;
      height: 100%;
      position: relative; }
    &::before {
      @include p;
      border: 2px solid $orange;
      width: calc(100% - 14px);
      height: calc(100% + 22px);
      top: -11px;
      left: 14px;
      z-index: 0; } } }

h2,
.inner h1 {
  font-size: 64px;
  font-weight: 700;
  line-height: 60px;
  text-transform: uppercase; }

.services {
  padding: 100px 0 117px;
  &__header {
    margin-bottom: 80px; }
  &-items {
    margin-bottom: 80px; }
  &__item {
    width: calc(100% / 3 - 20px);
    overflow: hidden;
    &:hover {
      .services__item-hidden {
        opacity: 1;
        top: 0; } }
    &-hidden {
      position: absolute;
      top: 0;
      left: 0;
      background: rgba($blue, 0.8);
