      height: 100%;
      padding: 20px;
      opacity: 0;
      box-sizing: border-box;
      @include tr; }
    &-image {
      width: 100%;
      height: 100%;
      object-fit: cover;
      &-part {
        height: 320px;
        position: relative;
        line-height: 0;
        margin-bottom: 20px; } }
    &-text {
      font-family: 'Open Sans', sans-serif;
