.order {
  padding: 60px 0 22px;
  position: relative;
  &::before {
    @include p;
    width: 100%;
    height: 100%;
    background: #F6F6F6;
    top: 0;
    left: 0; }
  .container {
    position: relative; }
  &__header {
    margin-bottom: 40px; }
  &__left {
    width: 440px; }
  &__right {
    position: absolute;
    top: -20px;
    right: -250px;
    line-height: 0;
    &::before {
      @include p;
      border: 2px solid $orange;
      width: calc(100% - 14px);
      height: calc(100% + 22px);
      top: -11px;
      left: 14px;
      z-index: 0; } }
  &__image {
    width: 660px;
    position: relative; }

  &-form {
    &__label {
      width: 100%;
      font-weight: 700;
      display: block;
      margin-bottom: 20px;
      position: relative;
      &-text {
        margin-bottom: 5px; }
      &-name {
        width: 260px; }
      &-phone {
        width: 160px; }
      &-comment {
        margin-bottom: 24px; }
      &-file {
        width: 240px;
        height: 40px;
        margin-bottom: 40px; }
      &-privacy {
        padding-left: 25px;
        font-family: 'Open Sans', sans-serif;
        font-size: 14px;
        font-weight: 400;
        line-height: 19px;
        color: $grey;
        letter-spacing: -0.33px;
        &::before, &::after {
          @include p;
          width: 16px;
          height: 16px;
          left: 0;
          top: 0;
          background: $blue;
          @include tr; }
        &::after {
          opacity: 0;
          background: url('../img/sprite.svg#check-white') no-repeat center;
          background-size: 11px; }
        &.active {
          &::after {
            opacity: 1; } }
        a {
          color: $grey;
          border-bottom: 1px solid rgba($grey, .5);
          &:hover {
            border-bottom-color: transparent; } } } }

    &__dropdown, &__input, &__textarea {
      width: 100%;
      height: 40px;
      background: #fff;
      border: 1px solid rgba(58, 121, 154, 0.15); }
    &__dropdown {
      border: 0;
      width: 260px;
      font-size: 17px;
      position: relative;
      &::before {
        @include p;
        width: 10px;
        height: 10px;
        background: url('../img/sprite.svg#dropdown') no-repeat center;
        background-size: 12px;
        top: 18px;
        right: 10px; }
      &-current {
        border: 1px solid rgba(58, 121, 154, 0.15);
        height: 40px;
        line-height: 38px;
        padding: 0 10px; }
      ul {
        display: none;
        position: relative;
        z-index: 10;
        background: #fff;
        li {
          padding: 10px;
          border: 1px solid rgba(58, 121, 154, 0.15);
          border-top: 0;
          @include tr;
          &:hover {
            background: rgba(58, 121, 154, 0.15); } } } }
    &__input, &__textarea {
      font-weight: 400;
      &::placeholder {
        color: $grey; } }
    &__input {
      padding: 0 10px;
      &-file {
        width: 0;
        height: 0;
        overflow: hidden;
        position: absolute;
        opacity: 0;
        top: 0;
        left: 0; } }
    &__textarea {
      height: 80px;
      resize: none;
      padding: 6px 10px; }
    &__file {
      &-wrap {
        width: 100%;
        position: relative;
        .name {
          border: 1px solid rgba(58, 121, 154, 0.5);
          height: 40px;
          padding: 0 10px 0 42px;
          font-weight: 500;
          color: $grey;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          line-height: 38px;
          &::before {
            @include p;
            width: 48px;
            height: 40px;
            top: -1px;
            left: 0;
            background: url('../img/sprite.svg#file') no-repeat center;
            background-size: 20px; } }
        .delete {
          width: 38px;
          height: 38px;
          position: absolute;
          top: 1px;
          left: 1px;
          cursor: pointer;
          display: flex;
          justify-content: center;
          align-items: center;
          background: #F6F6F6 url('../img/sprite.svg#trash') no-repeat center;
          background-size: 20px;
          font-size: 30px; } }
      &-text {
        width: 181px;
        font-size: 18px;
        font-weight: 400;
        line-height: 20px;
        color: $grey;
        margin-top: -1px; } }
    &__button {
      width: 100%;
      height: 50px;
      font-size: 24px;
      margin-bottom: 20px; } } }

